<template>
  <div class="container">
    <page-title first-title="分销商品列表"></page-title>
    <div class="button-wrapper">
      <!-- 文件上传 -->
      <el-upload
        class="uploadfil"
        :action="Upload"
        :on-success="getfilelist"
        :show-file-list="false"
        accept="application/vnd.ms-excel,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet"
      >
        <el-button type="primary">添加新商品</el-button>
      </el-upload>

      <el-button type="primary" @click="downLoad">导出</el-button>
    </div>
    <div class="search-wrapper">
      <el-tabs type="card" @tab-click="handleClick">
        <el-tab-pane v-for="item in retailState" :label="item.label" :key="item.id">
          <el-form :inline="true" class="demo-form-inline" :model="params">
            <el-form-item label="商品编号">
              <el-input v-model="params.goodsId" placeholder="商品编号" clearable></el-input>
            </el-form-item>
            <el-form-item label="商品名称">
              <el-input v-model="params.goodsName" placeholder="商品名称" clearable></el-input>
            </el-form-item>
            <el-form-item label="商家">
              <el-autocomplete
                class="inline-input"
                :fetch-suggestions="querySearch"
                placeholder="请输入内容"
                v-model="params.supplier"
                clearable
              ></el-autocomplete>
            </el-form-item>
            <el-form-item label="SPU货号">
              <el-input v-model="params.spu" placeholder="SPU货号" clearable></el-input>
            </el-form-item>
            <el-form-item label="活动名称">
              <el-input v-model="params.actName" placeholder="活动名称" clearable></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="primary" @click="search" icon="el-icon-search">搜索</el-button>
              <el-button type="primary" @click="clear" icon="el-icon-refresh-left">清空</el-button>
            </el-form-item>
          </el-form>

          <div class="list-wrapper">
            <el-table :data="retailsupplier_list" :header-cell-style="{ background: '#eef1f6' }" style="width: 100%">
              <!-- <el-table-column
          v-for="column in columns_list"
          :key="column.id"
          :prop="column.prop"
          :label="column.label"
        ></el-table-column> -->
              <el-table-column prop="goodsId" label="商品编码" width="50"></el-table-column>
              <el-table-column prop="spu" label="SPU货号"></el-table-column>
              <el-table-column label="商品图">
                <template slot-scope="scope">
                  <img :v-show="scope.row.goodsPic" :src="scope.row.goodsPic" width="50" height="50" />
                </template>
              </el-table-column>
              <el-table-column prop="goodsName" label="商品名称"></el-table-column>
              <el-table-column prop="createdAt" label="分销底价设置时间" :formatter="dateFormat"></el-table-column>
              <el-table-column label="本店售价/积分价">
                <template slot-scope="scope">
                  <span>￥{{ scope.row.ourShopPrice }}</span>
                  <span>Ⓗ {{ scope.row.ourShopCoin }}</span>
                </template>
              </el-table-column>
              <el-table-column label="分销底价/积分价">
                <template slot-scope="scope">
                  <span>￥{{ scope.row.realShopPrice }}</span>
                  <span>Ⓗ {{ scope.row.distributionCoin }}</span>
                </template>
              </el-table-column>
              <el-table-column label="+￥+Ⓗ">
                <template slot-scope="scope">
                  <span>￥{{ scope.row.differencePrice }}</span>
                  <span>Ⓗ {{ scope.row.differenceCoin }}</span>
                </template>
              </el-table-column>
              <el-table-column label="运营底价/积分价">
                <template slot-scope="scope">
                  <span>￥{{ scope.row.shopPrice }}</span>
                  <span>Ⓗ {{ scope.row.operationCoin }}</span>
                </template>
              </el-table-column>
              <el-table-column label="列表推荐排序值" width="70">
                <template slot-scope="scope">
                  <el-tooltip class="item" effect="dark" content="点击修改内容" placement="bottom">
                    <span :id="scope.row.goodsId + 'as'" @click="changeNum(scope.row.goodsId + 'a')">{{
                      scope.row.listRecommendSort
                    }}</span>
                  </el-tooltip>
                  <input
                    :id="scope.row.goodsId + 'a'"
                    v-model="scope.row.listRecommendSort"
                    type="text"
                    class="nums"
                    @blur="
                      saveNum(
                        scope.row.goodsId + 'a',
                        scope.row.goodsId,
                        scope.row.listRecommendSort,
                        'listRecommendSort'
                      )
                    "
                  />
                </template>
              </el-table-column>
              <el-table-column label="分类推荐排序值" width="70">
                <template slot-scope="scope">
                  <el-tooltip class="item" effect="dark" content="点击修改内容" placement="bottom">
                    <span :id="scope.row.goodsId + 'bs'" @click="changeNum(scope.row.goodsId + 'b')">{{
                      scope.row.categoryRecommendSort
                    }}</span>
                  </el-tooltip>
                  <input
                    :id="scope.row.goodsId + 'b'"
                    v-model="scope.row.categoryRecommendSort"
                    type="text"
                    class="nums"
                    @blur="
                      saveNum(
                        scope.row.goodsId + 'b',
                        scope.row.goodsId,
                        scope.row.categoryRecommendSort,
                        'categoryRecommendSort'
                      )
                    "
                  />
                </template>
              </el-table-column>
              <el-table-column prop="actName" label="活动名称"></el-table-column>
              <el-table-column prop="startTime" label="开始时间" :formatter="dateFormat"></el-table-column>
              <el-table-column prop="endTime" label="结束时间" :formatter="dateFormat"></el-table-column>
              <el-table-column label="活动运营底价/积分价">
                <template slot-scope="scope">
                  <span>￥{{ scope.row.actOperationPrice }}</span>
                  <span>Ⓗ {{ scope.row.actOperationCoin }}</span>
                </template>
              </el-table-column>
              <el-table-column
                prop="newcomerCommission"
                label="拉新奖励"
                width="50"
                :formatter="Moneyico"
              ></el-table-column>
              <el-table-column
                prop="activityCommission"
                label="活动奖励"
                width="50"
                :formatter="Moneyico"
              ></el-table-column>
              <el-table-column label="操作">
                <template slot-scope="scope">
                  <el-button @click="dialogshow(scope.row.goodsId, scope.row.isOnSale)" type="text" size="small">{{
                    buttonName(scope.row.isOnSale)
                  }}</el-button>
                  <el-button @click="pricehistory(scope.row.goodsId)" type="text" size="small">历史调价</el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>

          <el-pagination
            background
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-size="params.pageSize"
            layout="prev, pager, next, jumper"
            :total="total"
          ></el-pagination>
        </el-tab-pane>
      </el-tabs>
    </div>
    <!-- 提示消息 加入分销返佣-->
    <el-dialog class="retails" title="确认加入分销返佣" :visible.sync="dialogVisibleAdd" :show-close="false" center>
      <span class="message"
        >确定要加入商品吗？ <br />
        加入后分销返佣专区中将展示该商品</span
      >
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisibleAdd = false">取 消</el-button>
        <el-button type="primary" @click="changelist(goodIds, 1)">确 定</el-button>
      </span>
    </el-dialog>

    <!-- 提示消息 移出分销返佣-->
    <el-dialog class="retails" title="确认移除分销返佣" :visible.sync="dialogVisibleRemove" :show-close="false">
      <span class="message"
        >确定要移除该商品吗？<br />移除后分销返佣专区中将不再暂时该商品 <br />已经分享出去的分销商品链接将失效
      </span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisibleRemove = false">取 消</el-button>
        <el-button type="primary" @click="changelist(goodIds, 0)">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { RETAIL_STATE } from '../static'
import '../../../assets/js/jquery-3.4.1.min.js'
export default {
  name: 'RetailSupplierList',
  data() {
    return {
      retailsupplier_list: [],
      currentPage: 1,
      retailState: RETAIL_STATE,
      params: {
        pageNumber: 1,
        pageSize: 10,
        isOnSale: '1',
        goodsId: '',
        spu: '',
        goodsName: '',
        actName: '',
        supplier: ''
      },
      total: 0,
      dialogVisibleRemove: false,
      dialogVisibleAdd: false,
      Upload: process.env.VUE_APP_JAVASERVER_HOST + '/mlm/goods/list/excel',
      // Upload: "http://192.168.2.221:8090/mlm/goods/list/excel", //上传地址
      // dataurl: "http://192.168.2.221:8090",
      dataurl: '',
      goodIds: '',
      restaurants: []
    }
  },
  created() {
    this.getList()
  },
  mounted() {
    this.restaurants = this.getSer()
  },
  methods: {
    downLoad() {
      //导出下载excel
      let a = document.createElement('a')
      a.href = this.Upload
      a.click()
    },
    search() {
      this.params.pageNumber = 1
      this.getList()
    },
    getSer() {
      this.$api.get(this.dataurl + '/wo/merchant/userType', null, r => {
        if (r.code == 1) {
          var arrName = []
          r.data.suppliersList.forEach(function(value) {
            let item = {}
            item['value'] = value.suppliersName
            arrName.push(item)
          })
          this.restaurants = arrName
        } else {
          this.$message.error(r.msg)
        }
      })
    },
    getList() {
      this.replaceDatas(this.params)
      let pramstr = JSON.stringify(this.params)
        .replace(/:/g, '=')
        .replace('{', '')
        .replace('}', '')
        .replace(/,/g, '&')
        .replace(/"/g, '')
        .replace(/\s+/g, '')
      this.$api.get(this.dataurl + '/mlm/goods/list?' + pramstr, null, r => {
        if (r.code == 1) {
          this.retailsupplier_list = r.data.goodsMlmList
          this.total = r.data.totalElements
        } else {
          this.list = {}
          this.$message.error(r.msg)
        }
      })
    },
    replaceDatas(data) {
      for (let key in data) {
        if (data[key] == '' || data[key] == null) {
          delete data[key]
        }
      }
    },
    handleClick(tab) {
      this.clear()
      this.retailsupplier_list.length = []
      tab.index == '0' ? (this.params.isOnSale = '1') : (this.params.isOnSale = '0')
      console.log(this.params.isOnSale)
      this.getList()
    },
    dialogshow(a, b) {
      this.goodIds = a
      if (b == 0) {
        //加入分销返佣列表
        this.dialogVisibleAdd = true
      } else {
        //移除分销返佣列表
        this.dialogVisibleRemove = true
      }
    },
    changelist(a, n) {
      // 商品移除/加入列表 a:goodsId
      this.dialogVisibleAdd = false
      this.dialogVisibleRemove = false
      this.$api.put(this.dataurl + '/mlm/goods/' + a + '/is-on-sale?isOnSale=' + n, null, r => {
        if (r.code == 1) {
          this.getList()
        } else {
          this.$message.error(r.msg)
        }
      })
    },
    handleCurrentChange(currentPage) {
      this.params.pageNumber = currentPage
      this.getList()
    },
    dateFormat: function(row, column) {
      let date = row[column.property]
      return date ? this.utils.formatDate('YYYY-MM-DD HH:mm:ss', date * 1e3) : date
    },
    Moneyico: function(row, column) {
      let date = row[column.property]
      return date ? '￥' + date : ''
    },
    clear() {
      this.params.pageNumber = 1
      this.params.pageSize = 10
      this.params.goodsId = ''
      this.params.spu = ''
      this.params.goodsName = ''
      this.params.actName = ''
      this.params.supplier = ''
    },
    pricehistory(id) {
      let q = this.retailsupplier_list.findIndex(value => value.goodsId == id) //根据sku货号找到对应的数据条目
      // console.log(q)
      // console.log(this.retailsupplier_list)
      // console.log(this.retailsupplier_list[q])
      localStorage.setItem('msg', JSON.stringify(this.retailsupplier_list[q]))
      this.$router.push({ name: 'PriceHistory', params: { id: id } })
    },
    buttonName(n) {
      if (n == 0) {
        return '加入'
      } else {
        return '移除'
      }
    },
    changeNum(n) {
      $('#' + n).show()
      $('#' + n + 's').hide()
    },
    saveNum(n, id, item, str) {
      $('#' + n).hide()
      $('#' + n + 's').show()
      isNaN(Number(item)) || Number(item) == 0 ? (item = 0) : item
      let str1 = ''
      let Strs = ''
      // console.log(str)
      str == 'listRecommendSort' ? (str1 = 'list-recommend-sort') : (str1 = 'category-recommend-sort')
      Strs = this.dataurl + '/mlm/goods/' + id + '/' + str1 + '?' + str + '=' + item
      this.$api.put(Strs, null, r => {
        if (r.code == 1) {
          console.log('修改成功')
        } else {
          this.$message.error(r.msg)
        }
      })
    },
    // 文件上传成功回调函数
    getfilelist(response) {
      let showcon = ''
      if (response.code == 0) {
        showcon = response.msg
      } else {
        var htmlstr = '',
          failinorstr = '',
          failist = ''
        response.failInfos.forEach(item => {
          failist += `
          <tr>
            <td>${item.failGoodsNum}</td>
            <td>${item.failMsg}</td>
          </tr>`
        })
        failinorstr = `
        <div class="failist">
          <p>上传失败商品列表</p>
          <table width="100%" cellspacing="1" cellpadding="10">
            <tr><th>商品编码</th><th width="80%">消息提示</th></tr>
            ${failist}
          </table>
        </div>
        `
        htmlstr = `
          <div class="filealert">
            <p>文件上传结果：</p>
            <p class="green">成功：<span>${response.successCount}</span> 条</p>
            <p class="red">失败：<span>${response.failCount}</span> 条</p>
          </div>
        `
        if (response.failCount > 0) {
          showcon = htmlstr + failinorstr
        } else {
          showcon = htmlstr
        }
      }
      this.$alert(showcon, '添加新商品', {
        dangerouslyUseHTMLString: true,
        confirmButtonText: '确定',
        callback: () => {
          //若有上传成功条目则重新加载分销商品列表
          if (response.successCount > 0) {
            this.getList()
          }
        }
      })
    },
    querySearch(queryString, cb) {
      var restaurants = this.restaurants
      var results = queryString ? restaurants.filter(this.createFilter(queryString)) : restaurants
      // 调用 callback 返回建议列表的数据
      cb(results)
    },
    createFilter(queryString) {
      return restaurant => {
        return restaurant.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0
      }
    }
  }
}
</script>

<style lang="scss">
.button-wrapper {
  text-align: right;
  padding: 20px 20px 10px;
}
.header {
  margin-bottom: 10px;
}
.container {
  // min-width: 1366px;
  .search-wrapper {
    padding: 10px;
    margin-bottom: 10px;
    background: #fff;
    border: 1px solid #eee;
    .el-form-item {
      margin-bottom: 10px;
    }
  }
  .list-wrapper {
    margin-top: 10px;
    .nums {
      width: 40px;
      display: none;
    }
    .el-table .cell {
      line-height: 1.5em;
      span {
        display: block;
      }
    }
  }
}
.el-pagination {
  margin-top: 10px;
  text-align: center;
}
.retails {
  .el-dialog__header {
    background: #505763;
    span {
      color: #fff;
    }
  }
  .message {
    text-align: center;
    display: block;
    font-weight: bold;
    line-height: 2em;
    font-size: 16px;
  }
}
.el-button--text {
  width: 100%;
  display: block;
  margin: 0;
  text-align: center;
  padding: 4px 0;
}
.el-table td,
.el-table th {
  padding: 6px 0;
}
.uploadfil {
  display: inline-block;
  vertical-align: top;
  margin-right: 10px;
}
.filealert {
  line-height: 2em;
  .green span {
    color: darkgreen;
  }
  .red span {
    color: darkred;
  }
}
.failist {
  line-height: 1.2em;
  text-align: center;
  p {
    margin-top: 10px;
  }
  th {
    background: #ebeef5;
  }
  td {
    background: #f5f5f5;
  }
}
</style>
