<template>
  <div class="container">
    <page-title first-title="历史调价信息"></page-title>
    <div class="list-wrapper">
      <el-table :data="priceHistory" stripe id="table_history" :header-cell-style="{ background: '#eef1f6' }">
        <el-table-column prop="goodsId" label="商品编码"></el-table-column>
        <el-table-column prop="spu" label="SPU货号"></el-table-column>
        <el-table-column label="商品图">
          <template slot-scope="scope">
            <img v-show="scope.row.goodsPic" :src="scope.row.goodsPic" width="50" height="50" />
          </template>
        </el-table-column>
        <el-table-column prop="goodsName" label="商品名称"></el-table-column>
        <el-table-column prop="createdAt" label="分销底价设置时间" :formatter="dateFormat"></el-table-column>
        <el-table-column label="本店售价/积分价">
          <template slot-scope="scope">
            <span>￥{{ scope.row.ourShopPrice }}</span>
            <span>Ⓗ {{ scope.row.ourShopCoin }}</span>
          </template>
        </el-table-column>
        <el-table-column label="分销底价/积分价">
          <template slot-scope="scope">
            <span>￥{{ scope.row.realShopPrice }}</span>
            <span>Ⓗ {{ scope.row.distributionCoin }}</span>
          </template>
        </el-table-column>
        <el-table-column label="+￥+Ⓗ">
          <template slot-scope="scope">
            <span>￥{{ scope.row.differencePrice }}</span>
            <span>Ⓗ {{ scope.row.differenceCoin }}</span>
          </template>
        </el-table-column>
        <el-table-column label="运营底价/积分价">
          <template slot-scope="scope">
            <span>￥{{ scope.row.shopPrice }}</span>
            <span>Ⓗ {{ scope.row.operationCoin }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="actName" label="活动名称"></el-table-column>
        <el-table-column prop="startTime" label="开始时间" :formatter="dateFormat"></el-table-column>
        <el-table-column prop="endTime" label="结束时间" :formatter="dateFormat"></el-table-column>
        <el-table-column label="活动运营底价/积分价">
          <template slot-scope="scope">
            <span>￥{{ scope.row.actOperationPrice }}</span>
            <span>Ⓗ {{ scope.row.actOperationCoin }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="newcomerCommission" label="拉新奖励" :formatter="Moneyico"></el-table-column>
        <el-table-column prop="activityCommission" label="活动奖励" :formatter="Moneyico"></el-table-column>
      </el-table>
    </div>
    <!-- <el-pagination
      background
      @current-change="handleCurrentChange"
      :current-page="currentPage"
      :page-size="params.size"
      layout="prev, pager, next, jumper"
      :total="total"
    ></el-pagination> -->
  </div>
</template>
<script>
export default {
  name: 'PriceHistory',
  data() {
    return {
      good_id: this.$route.params.id ? this.$route.params.id : '',
      priceHistory: [],
      // currentPage: 1,
      // dataurl: "http://192.168.2.221:8090",
      dataurl: '',
      total: 0
    }
  },
  created() {
    this.getList()
  },
  methods: {
    getList() {
      this.$api.get(this.dataurl + '/mlm/goods/price/history/' + this.good_id, null, r => {
        if (r.code == 1) {
          this.priceHistory = r.data
          console.log(this.priceHistory)
          let msg = JSON.parse(localStorage.getItem('msg'))
          this.priceHistory[0]['goodsName'] = msg.goodsName
          this.priceHistory[0]['goodsPic'] = msg.goodsPic
          this.priceHistory[0]['goodsId'] = msg.goodsId
          this.priceHistory[0]['spu'] = msg.spu
        } else {
          this.$message.error(r.msg)
        }
      })
    },
    Moneyico: function(row, column) {
      let date = row[column.property]
      return date ? '￥' + date : ''
    },
    dateFormat: function(row, column) {
      let date = row[column.property]
      return date ? this.utils.formatDate('YYYY-MM-DD HH:mm:ss', date * 1e3) : date
    }
  }
}
</script>
<style lang="scss" scoped>
.el-pagination {
  margin-top: 10px;
  text-align: center;
}
#table_history {
  .el-table_1_column_9 {
    border-right: 1px solid #ccc;
  }
  span {
    display: block;
  }
}
</style>
